.topbar{
    width:100%;
    height: 50px;
    background-color: white;
    position: sticky;top: 0;z-index: 999;
}
.topbarWrapper{
    height:50%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    font-weight: bold;
    font-size: 30px;
    color: rgb(2, 43, 43);
    cursor: pointer;

}
.topRight{
    display: flex;
    align-items: center;
    margin-right:5px;
    margin-top: 20px;
    color: #555;
}

.topbarIconContainer{
    position: relative;
    margin-right: 5px;
}
.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top:  -5px;
    right: 5px;
    background-color:red;
    color:white;
    border-radius:50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50px;
    cursor: pointer;

}